/** @format */

import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { chooseUsingCard } from '../../redux/slice/ToolsSlicer';
import { ChooseTab } from '../../redux/slice/navBarSlice';

const foxs = [
	'./img/fox-1.png',
	'./img/fox-2.png',
	'./img/fox-3.png',
];

const chicks = ['./img/chick-1.png', './img/chick-2.png', './img/chick-3.png'];
const sheepNest = ['./img/sheep-nest.png'];

export default function MapFox(props) {
	const foxUsing = useSelector((state) => state.stars.foxUsing);
	const [state, setstate] = useState();
	const dispatch = useDispatch();

	const handleMouseEnter = (i) => {
		setstate(i);
	};

	const handleMouseLeave = () => {
		setstate(-1);
	};

	const handleChoose = (index) => {
		dispatch(ChooseTab(0));
		dispatch(chooseUsingCard(index));
	};

	return (
		<>
			<div className='game-container__image'>
				{foxUsing.map((fox, index) => {
					switch (fox.template_id) {
						case 298614:
							return (
								<img
									className={`fox ${
										index === state ? 'fox-hover' : null
									} fox-${((index + props.xRandom) % 3) + 1}`}
									src={foxs[(index + props.xRandom) % 3]}
									alt={index}
									key={index}
									onMouseEnter={() => handleMouseEnter(index)}
									onMouseLeave={() => handleMouseLeave()}
									onClick={() => handleChoose(index)}
								/>
							);
						case 298613:
							return (
								<img
									className={`fox ${
										index === state ? 'fox-hover' : null
									} chick-${((index + props.xRandom) % 3) + 1}`}
									src={chicks[(index + props.xRandom) % 3]}
									alt={index}
									key={index}
									onMouseEnter={() => handleMouseEnter(index)}
									onMouseLeave={() => handleMouseLeave()}
									onClick={() => handleChoose(index)}
								/>
							);
						case 298612:
							return (
								<img
									className={`sheep-nest fox ${
										index === state ? 'fox-hover' : null
									}`}
									src={sheepNest[0]}
									alt={index}
									key={index}
									onMouseEnter={() => handleMouseEnter(index)}
									onMouseLeave={() => handleMouseLeave()}
									onClick={() => handleChoose(index)}
								/>
							);
						default:
							break;
					}
					return null;
				})}
			</div>
		</>
	);
}
