/** @format */

import React, { useRef, useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import Button from '../Button/Button';
import closeButton from '../../assets/img/close-button.png';
import { useSprings, animated, to } from 'react-spring';
import { useDrag } from 'react-use-gesture';
import { useDispatch, useSelector } from 'react-redux';

import {
	cancelLoading,
	setBackgroundUpdate,
} from '../../redux/slice/GameSlicer';
import { toggleModal, setErrorMessage } from '../../redux/slice/modalSlice';

import { deleteFlash, setFlash } from '../../redux/slice/FlashSlicer';
import getErrorMessages from '../../utils/getErrorMessages';
import { giveProduceing, startProduceing } from '../../redux/slice/ProduceingSlicer';
import ProgressBar from '../ProgressBar/ProgressBar';
import { getChestAssetsByTemplate } from '../../redux/slice/AtomicSlicer';
import { getTransaction } from '../../redux/slice/StarsSlicer';
import CancelProduceingModal from '../Modal/CancelProduceingModal';

const serverIMG = 'https://mypinata.cloud/ipfs/';

export default function DogProduceing(props) {
	const finalRef = useRef();
	const finalRefs = useRef();
	const [isCountDown, setCountdown] = useState(true);

	const produceings = useSelector((state) => state.produceing.produceings);

	const [dogMale, setDogMale] = useState(null);
	const [dogFemale, setDogFemale] = useState(null);

	const [dogProduceing, setDogProduceing] = useState(false);
	const [downProduceing, setDownProduceing] = useState();
	const [modalShowing, setModalShowing] = useState(false);

	const [dogData, setDogData] = useState(props.data);

	const produceingConfig = useSelector((state) => state.produceing.produceingConfig);
	const plasmaList = useSelector((state) =>
		getChestAssetsByTemplate(
			state.atomic.plasmas,
			produceingConfig[0].consumed_card + ''
		)
	);

	const dispatch = useDispatch();

	const isPlasma = plasmaList?.length > 0;
	const isPair = dogMale && dogFemale;
	const produceText = isPair
		? isCountDown === true
			? isPlasma
				? 'Produce'
				: 'No Plasma'
			: 'Countdown'
		: 'No Pair';
	const isProduceable = produceText === 'Produce' || 'disabled';
	const isCancelable =
		(produceings.length >= 1 && isPair && isCountDown === true) || 'disabled';

	useEffect(() => {
		setDogData(props.data);
		if (props.dogProduceing.length > 0 && props.dogProduceing.length <= 2) {
			setDogProduceing(true);
			props.dogProduceing.forEach((item) => {
				if (item.gender === 1) {
					setDogMale(item);
				} else if (item.gender === 2) {
					setDogFemale(item);
				}
			});
		} else {
			setDogProduceing(false);
			setDogMale(null);
			setDogFemale(null);
		}
	}, [props.dogProduceing, produceings, props.data]);

	useEffect(() => {
		setCountdown(
			Date.now() - (produceings[0]?.next_availability || 0) * 1000 >= 0 ||
				'disabled'
		);
		return () => {
			setCountdown(true);
		};
	}, [produceings]);

	const handleProduce = () => {
		if (produceings.length >= 1) {
			handleClaimProduce();
		} else {
			handleStartProduce();
		}
	};

	const handleStartProduce = async () => {
		const flash_id = Date.now();
		try {
			dispatch(cancelLoading(false));
			const flashMessage = {
				id: flash_id,
				content: `Start Produceing your ${dogMale.name} and ${dogFemale.name} `,
				timeout: 30000,
			};
			dispatch(setFlash(flashMessage));
			const data = {
				dad: dogMale.asset_id,
				mother: dogFemale.asset_id,
			};
			const resultAction = await dispatch(startProduceing(data)).unwrap();
			if (resultAction.transaction_id) {
				const inform =
					'Pairing your Bull and Dairy Dog successfully. After this Countdown, you can produce them!';
				dispatch(setErrorMessage(inform));
				dispatch(toggleModal(true));
			}
		} catch (error) {
			getErrorMessages(error, dispatch, setErrorMessage, toggleModal);
		} finally {
			dispatch(deleteFlash(flash_id));
			dispatch(setBackgroundUpdate(true));
		}
	};

	const handleClaimProduce = async () => {
		const flash_id = Date.now();
		try {
			dispatch(cancelLoading(false));
			if (isProduceable !== 'disabled') {
				const flashMessage = {
					id: flash_id,
					content: `Produceing your ${dogMale.name} and ${dogFemale.name} `,
					timeout: 30000,
				};
				dispatch(setFlash(flashMessage));
				const data = {
					dad: dogMale.asset_id,
					mother: dogFemale.asset_id,
					plasma: plasmaList,
				};
				const resultAction = await dispatch(giveProduceing(data)).unwrap();
				if (resultAction.transaction_id) {
					const flashMessage2 = {
						id: flash_id,
						content: `Successfull produceing`,
						timeout: 1500,
					};
					dispatch(setFlash(flashMessage2));
					if (
						produceings[0] &&
						produceings[0]?.times_claimed + 1 >= produceingConfig[0].required_claims
					) {
						const result = await dispatch(
							getTransaction(resultAction.transaction_id)
						).unwrap();
						if (result.claim) {
							const inform =
								"You've just harvested " + result.claim.quantity + ' Baby Caft';
							dispatch(setErrorMessage(inform));
						} else {
							dispatch(setErrorMessage('You got your rewards'));
						}
						dispatch(toggleModal(true));
					}
				}
			}
		} catch (error) {
			getErrorMessages(error, dispatch, setErrorMessage, toggleModal);
		} finally {
			dispatch(deleteFlash(flash_id));
			dispatch(setBackgroundUpdate(true));
		}
	};
	const handleCancel = async () => {
		if (isCancelable !== 'disabled') {
			setModalShowing(true);
		}
	};

	const too = (i) => ({
		x: 0,
		y: 0,
		scale: 1,
	});
	const from = (i) => ({ x: 0, rot: 0, scale: 1, y: 0 });

	const [propsPos, api] = useSprings(dogData.length, (i) => ({
		...too(i),
		from: from(i),
	}));

	const bind = useDrag(({ args: [index], tap }) => {
		let dogChoosePos;
		let malePos;
		let femalePos;
		let x;
		let y;
		let scale;

		if (tap) {
			const init = document.getElementById(`dogs-select-${index}`);

			if (init != null) {
				dogChoosePos = init.getBoundingClientRect();
			}
			if (finalRef.current && finalRefs.current) {
				malePos = finalRef.current.getBoundingClientRect();

				femalePos = finalRefs.current.getBoundingClientRect();
			}
			api.start((i) => {
				if (index !== i) return;

				if (props.data[i].gender === 2) {
					if (dogFemale !== null && dogFemale !== props.data[i]) {
						return;
					}
					if (dogFemale === props.data[i]) {
						setDogFemale(null);
						scale = 1;
						x = (dogChoosePos?.x - femalePos?.x) * 1;
						y = (dogChoosePos?.y - femalePos?.y) * 1;
					} else {
						setDogFemale(props.data[i]);
						scale = 1.4;
						x = (dogChoosePos?.x - femalePos?.x) * -1;
						y = (dogChoosePos?.y - femalePos?.y) * -1;
					}
				} else if (props.data[i].gender === 1) {
					if (dogMale !== null && dogMale !== props.data[i]) {
						return;
					}
					if (dogMale === props.data[i]) {
						scale = 1;
						setDogMale(null);
						x = (dogChoosePos?.x - malePos?.x) * 1;
						y = (dogChoosePos?.y - malePos?.y) * 1;
					} else {
						scale = 1.4;

						setDogMale(props.data[i]);
						x = (dogChoosePos?.x - malePos?.x) * -1;
						y = (dogChoosePos?.y - malePos?.y) * -1;
					}
				}
				return {
					x,
					y,
					scale,
					delay: undefined,
				};
			});
		}
	});

	const ref = useRef(null);

	const handleClose = () => {
		setDownProduceing('dogs-produceing-down');
		setTimeout(() => {
			props.isClose();
			setDownProduceing(null);
		}, 500);
	};

	return ReactDOM.createPortal(
		<React.Fragment>
			<div className='modal-wrapper' tabIndex={-1} role='dialog'>
				{modalShowing === true && (
					<CancelProduceingModal
						HandleCancel={() => setModalShowing(false)}
						dogMale={dogMale}
						dogFemale={dogFemale}
						handelDogMale={() => setDogMale(null)}
						handelDogFeMale={() => setDogFemale(null)}
						handleDogProduceing={() => setDogProduceing(false)}
					/>
				)}
				<div className={`dogs-produceing ${downProduceing}`} ref={ref}>
					<div
						className='dogs-produceing__header--tilte'
						style={{
							backgroundImage: 'url(./img/border-button.png)',
						}}>
						Produceing
					</div>
					<img
						src={closeButton}
						alt='Close'
						className='close-dogs-modal image-button'
						onClick={() => handleClose()}
					/>
					<div className='dogs-produceing__list'>
						<section className='dogs-vertical-carousel-container'>
							<div
								className='dogs-produceing__drop'
								style={{
									backgroundImage: 'url(/img/market-paper.png)',
								}}>
								<div className='dogs-produceing__drop--type'>
									<div className='dogs-produceing__drop--img' ref={finalRefs}>
										{!dogProduceing ? (
											<>
												{!dogFemale && (
													<img
														className='dogs-produceing__drop--icon'
														src='./img/female.png'
														alt=''
														id='dog-female'
													/>
												)}
											</>
										) : (
											<>
												{!dogFemale ? (
													<img
														className='dogs-produceing__drop--icon'
														src='./img/female.png'
														alt=''
														id='dog-female'
													/>
												) : (
													<img
														className='dogs-produceing__drop--icon'
														src={serverIMG + dogFemale.img}
														style={{
															transform: `scale(1.9)`,
														}}
														alt=''
														id='dog-female'
													/>
												)}
											</>
										)}

										<img
											className='dogs-produceing__drop--frames'
											src='./img/tall-card-border.png'
											alt=''
										/>
									</div>
									<div className='dogs-produceing__drop--img' ref={finalRef}>
										{!dogProduceing ? (
											<>
												{!dogMale && (
													<img
														className='dogs-produceing__drop--icon'
														src='./img/male.png'
														alt=''
														id='male'
													/>
												)}
											</>
										) : (
											<>
												{!dogMale ? (
													<img
														className='dogs-produceing__drop--icon'
														src='./img/male.png'
														alt=''
														id='male'
													/>
												) : (
													<img
														className='dogs-produceing__drop--icon'
														src={serverIMG + dogMale.img}
														alt=''
														style={{
															transform: `scale(1.9)`,
														}}
														id='dog-female'
													/>
												)}
											</>
										)}

										<img
											className='dogs-produceing__drop--frames'
											src='./img/tall-card-border.png'
											alt=''
										/>
									</div>
								</div>
								<div className='produceing-progress'>
									<ProgressBar
										currentStack={produceings[0]?.times_claimed}
										maxStack={produceings[0]?.required_claims}
										next_availability={produceings[0]?.next_availability}
										handleFinish={setCountdown}
										height={1.8}
									/>
								</div>
								<div className='dogs-produceing__drop-button'>
									<Button
										text={produceText}
										atr='semi-short'
										isDisabled={isProduceable}
										wrapperClassname='full-width'
										handleClick={handleProduce}
									/>
									<Button
										text='Cancel'
										atr='semi-short red'
										isDisabled={isCancelable}
										wrapperClassname='full-width'
										handleClick={handleCancel}
									/>
								</div>
							</div>
							{propsPos.map(({ x, y, scale }, i) => (
								<animated.div
									{...bind(i)}
									key={i}
									style={{
										transform: to(
											[x, y, scale],
											(x, y, scale) => `translate3d(${x}px,${y}px,0)`
										),
										position: 'relative',
										height: 'max-content',
									}}
									id={`dogs-select-${i}`}>
									<animated.img
										style={{
											transform: to(
												[x, y, scale],
												(x, y, scale) =>
													`scale(${scale}) ${
														scale === 1.4
															? 'translate3d(13%,14%,0)'
															: 'translate3d(0,0,0)'
													}`
											),
										}}
										// src={serverIMG + props.data[i].img}
										src={serverIMG + dogData[i].img}
										alt={i}
										key={i}
										className='dogs-select'
									/>
								</animated.div>
							))}
						</section>
					</div>
				</div>
				<div className='click-out-side' onClick={() => handleClose()} />
			</div>
		</React.Fragment>,
		document.body
	);
}

