/** @format */

import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { chooseUsingCard } from '../../redux/slice/ToolsSlicer';
import { ChooseTab } from '../../redux/slice/navBarSlice';
import DogProduceing from '../Produceing/DogProduceing';
import Button from '../../components/Button/Button';

const babyCaft = ['./img/babyCaft-1.png', './img/babyCaft-2.png'];
const caft = ['./img/caft-1.png', './img/caft-2.png'];
const dogs = ['./img/dog-1.png', './img/dog-2.png'];

export default function MapDog(props) {
	const dogUsing = useSelector((state) => state.stars.dogUsing);
	const produceings = useSelector((state) => state.produceing.produceings);

	const [state, setstate] = useState();
	const [isShowing, setIsShowing] = useState(false);

	const oldDogs = dogUsing.filter(
		(dog) => dog.name.includes('Bull') || dog.name.includes('Dairy')
	);

	const oldDog = oldDogs.filter((oldDogItem) => {
		return (
			produceings[0]?.bearer_id !== oldDogItem.asset_id &&
			produceings[0]?.partner_id !== oldDogItem.asset_id
		);
	});

	const dogProduceing = dogUsing.filter(
		(dog) =>
			dog.asset_id.includes(produceings[0]?.bearer_id) ||
			dog.asset_id.includes(produceings[0]?.partner_id)
	);

	const dispatch = useDispatch();

	const handleMouseEnter = (i) => {
		setstate(i);
	};

	const handleMouseLeave = () => {
		setstate(-1);
	};

	const handleChoose = (index) => {
		dispatch(ChooseTab(0));
		dispatch(chooseUsingCard(index));
	};

	return (
		<>
			<div className='game-container__image'>
				<div className='button-dog-produceing'>
					<Button
						text='Produceing'
						handleClick={() => setIsShowing(true)}
						atr='semi-long dog'
					/>
				</div>
				{dogUsing.map((dog, index) => {
					if (dog.name.includes('Baby'))
						return (
							<img
								className={`${index === state ? 'dog dog-hover' : null} dogs-${
									((index + props.xRandom) % 6) + 1
								}`}
								src={babyCaft[(index + props.xRandom) % 3]}
								alt={index}
								key={index}
								onMouseEnter={() => handleMouseEnter(index)}
								onMouseLeave={() => handleMouseLeave()}
								onClick={() => handleChoose(index)}
							/>
						);
					if (dog.name.includes('Wolf'))
						return (
							<img
								className={`${index === state ? 'dog dog-hover' : null} dogs-${
									((index + props.xRandom) % 6) + 1
								}`}
								src={caft[(index + props.xRandom) % 2]}
								alt={index}
								key={index}
								onMouseEnter={() => handleMouseEnter(index)}
								onMouseLeave={() => handleMouseLeave()}
								onClick={() => handleChoose(index)}
							/>
						);
					if (dog.name.includes('Bull') || dog.name.includes('Dairy'))
						return (
							<img
								className={`${index === state ? 'dog dog-hover' : null} dogs-${
									((index + props.xRandom) % 6) + 1
								}`}
								src={dogs[(index + props.xRandom) % 2]}
								alt={index}
								key={index}
								onMouseEnter={() => handleMouseEnter(index)}
								onMouseLeave={() => handleMouseLeave()}
								onClick={() => handleChoose(index)}
							/>
						);
					return null;
				})}
			</div>
			{isShowing && (
				<DogProduceing
					isClose={() => setIsShowing(false)}
					data={oldDog}
					produceings={produceings}
					dogProduceing={dogProduceing}
				/>
			)}
		</>
	);
}
