/** @format */

import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';

import { game } from './authSlicer';

export const startProduceing = createAsyncThunk(
	'produceing/startProduceing',
	async (data) => {
		for (let count = 0; count < 3; count++) {
			try {
				let respond = await game.produceingStart(data.dad, data.mother);
				return respond;
			} catch (error) {
				if (
					(error.message?.includes('undefined') ||
						error.message?.includes('Failed to fetch')) &&
					count < 2
				)
					continue;
				throw error;
			}
		}
	},
	{
		condition: (data, { getState, extra }) => {
			const { produceing } = getState();
			const fetchStatus = produceing.requests.filter(
				(__item_id) => __item_id === data.star
			);
			if (fetchStatus.length !== 0) {
				return false;
			}
		},
	}
);

export const giveProduceing = createAsyncThunk(
	'produceing/giveProduceing',
	async (data) => {
		for (let count = 0; count < 3; count++) {
			try {
				let respond = await game.produceingClaim(
					data.dad,
					data.mother,
					data.plasma[count % data.plasma.length]
				);
				return respond;
			} catch (error) {
				if (
					(error.message?.includes('undefined') ||
						error.includes("Sender doesn't ") ||
						error.message?.includes('Failed to fetch')) &&
					count < 2
				)
					continue;
				throw error;
			}
		}
	},
	{
		condition: (data, { getState, extra }) => {
			const { produceing } = getState();
			const fetchStatus = produceing.requests.filter(
				(__item_id) => __item_id === data.star
			);
			if (fetchStatus.length !== 0) {
				return false;
			}
		},
	}
);
export const cancelProduceing = createAsyncThunk(
	'produceing/cancelProduceing',
	async (mother_id) => {
		for (let count = 0; count < 3; count++) {
			try {
				let respond = await game.produceingCancel(mother_id);
				return respond;
			} catch (error) {
				console.log('error ', error);

				if (
					(error?.message?.includes('undefined') ||
						error?.message?.includes('Failed to fetch')) &&
					count < 2
				)
					continue;
				throw error;
			}
		}
	},
	{
		condition: (mother_id, { getState, extra }) => {
			const { produceing } = getState();
			const fetchStatus = produceing.requests.filter(
				(__item_id) => __item_id === mother_id
			);
			if (fetchStatus.length !== 0) {
				return false;
			}
		},
	}
);

export const getProduceingConf = createAsyncThunk(
	'produceing/getProduceingConf',
	async () => {
		const EquipConfigs = await game.getProduceingConf();
		return EquipConfigs;
	}
);
export const getProduceings = createAsyncThunk(
	'produceing/getProduceings',
	async () => {
		return await game.getProduceings();
	}
);
export const getPair = (state, bearer_id, partner_id) =>
	state.produceings.find((produce) => produce.id === bearer_id);
export const ProduceingSlice = createSlice({
	name: 'produceing',
	initialState: {
		produceingConfig: [],
		produceings: [],
		status: 'idle',
		isShowing: false,
		requests: [],
		error: '',
	},
	reducers: {
		setShow: (state, action) => {
			state.isShowing = action.payload;
		},
	},
	extraReducers: (builder) => {
		builder
			.addCase(getProduceingConf.pending, (state, action) => {
				state.status = 'loading';
			})
			.addCase(getProduceingConf.fulfilled, (state, action) => {
				state.status = 'loaded';
				state.produceingConfig = action.payload;
			})
			.addCase(getProduceingConf.rejected, (state, action) => {
				state.status = 'failed';
				state.error = action.error.message;
				// console.log(action.error.message)
			})
			.addCase(getProduceings.pending, (state, action) => {
				state.status = 'loading';
			})
			.addCase(getProduceings.fulfilled, (state, action) => {
				state.status = 'loaded';
				let tempProduceings = [];
				for (let item in action.payload) {
					const temp = state.produceingConfig.find(
						(conf) => conf.name === action.payload[item].name
					);
					tempProduceings.push(Object.assign(action.payload[item], temp));
				}
				state.produceings = tempProduceings;
			})
			.addCase(getProduceings.rejected, (state, action) => {
				state.status = 'failed';
				state.error = action.error.message;
			})
			.addCase(giveProduceing.pending, (state, action) => {
				state.status = 'loading';
				state.requests.push(action.meta.arg);
			})
			.addCase(giveProduceing.fulfilled, (state, action) => {
				state.status = 'loaded';
				const index = state.requests.indexOf(action.meta.arg);
				state.requests.splice(index, 1);
			})
			.addCase(giveProduceing.rejected, (state, action) => {
				state.status = 'failed';
				state.error = action.error.message;

				const index = state.requests.indexOf(action.meta.arg);
				state.requests.splice(index, 1);
			})

			.addCase(startProduceing.pending, (state, action) => {
				state.status = 'loading';
				state.requests.push(action.meta.arg);
			})
			.addCase(startProduceing.fulfilled, (state, action) => {
				state.status = 'loaded';
				const index = state.requests.indexOf(action.meta.arg);
				state.requests.splice(index, 1);
			})
			.addCase(startProduceing.rejected, (state, action) => {
				state.status = 'failed';
				state.error = action.error.message;

				const index = state.requests.indexOf(action.meta.arg);
				state.requests.splice(index, 1);
			})

			.addCase(cancelProduceing.pending, (state, action) => {
				state.status = 'loading';
				state.requests.push(action.meta.arg);
			})
			.addCase(cancelProduceing.fulfilled, (state, action) => {
				state.status = 'loaded';
				const index = state.requests.indexOf(action.meta.arg);
				state.requests.splice(index, 1);
			})
			.addCase(cancelProduceing.rejected, (state, action) => {
				state.status = 'failed';
				state.error = action.error.message;

				const index = state.requests.indexOf(action.meta.arg);
				state.requests.splice(index, 1);
			});
	},
});
export const { setShow } = ProduceingSlice.actions;

export default ProduceingSlice.reducer;
