/** @format */

import React from 'react';
// import './index.scss';
import GameCard from '../../components/GameCard/GameCard';
import { useSelector } from 'react-redux';

import EmtyTab from '../../components/EmptyTab/EmtyTab';
import { getCanBurn } from '../../redux/slice/PlasmasSlicer';

export default function Chest() {
	const usingItems = useSelector((state) => state.tools.usingItems);
	const usingBadges = useSelector((state) => state.badge.usingBadges);
	const toolChest = useSelector((state) => state.atomic.tools);
	const ticketsChest = useSelector((state) => state.atomic.tickets);
	const badgeChest = useSelector((state) => state.atomic.memberships);
	const starsChest = useSelector((state) => state.atomic.farmstars);
	const stationChest = useSelector((state) => state.atomic.farmstation);
	const plantsChest = useSelector((state) => state.atomic.plants);
	const plasmasChest = useSelector((state) => state.atomic.plasmas);
	const packsChest = useSelector((state) => state.atomic.packs);
	const refundChest = useSelector((state) => state.atomic.refundChest);
	const coinConfig = useSelector((state) => state.coin.coinConfig);
	const oxygen = useSelector((state) => state.user.balances.oxygen);
	// const plantsUsing = useSelector((state) => state.plants.plantsUsing);
	// const dogUsing = useSelector((state) => state.stars.dogUsing);
	// const foxUsing = useSelector((state) => state.stars.foxUsing);
	const canBurn = useSelector((state) => getCanBurn(state.plasmas));
	const chestItems = usingBadges
		?.concat(usingItems)
		?.concat(ticketsChest)
		?.concat(refundChest)
		?.concat(plantsChest)
		?.concat(badgeChest)
		?.concat(starsChest)
		?.concat(stationChest)
		?.concat(plasmasChest)
		?.concat(coinConfig)
		?.concat(toolChest)
		?.concat(packsChest);
	// ?.concat(dogUsing)
	// ?.concat(foxUsing)
	// ?.concat(plantsUsing)
	// ?.concat(dogUsing)
	// ?.concat(foxUsing)
	const isPlayable = chestItems.length > 0;

	return isPlayable ? (
		<>
			<section className='chest-container'>
				<div className='chest-content'>
					<div className='chest-card-list'>
						{chestItems.map(
							(card, index) =>
								card.asset_id && (
									<GameCard
										card={card}
										key={index}
										oxygen={oxygen}
										canBurn={canBurn}
									/>
								)
						)}
					</div>
				</div>
			</section>
			{/* <div className="wapper"></div> */}
		</>
	) : (
		// <div className="chest-container"></div>
		<EmtyTab />
	);
}
